import React, { Component } from 'react';

import { Document } from '../../components/all'

import './index.scss';

class chart extends Component {
    constructor(props) {
        super(props);

    }

    componentDidMount() {
        const isRetina = window.devicePixelRatio > 1 ? true : false;

        const chart = document.getElementById('chart');
        const ctx = chart.getContext('2d');

        ctx.scale(2, 2);

        const pointer = document.getElementById('pointer');
        const pointerCtx = pointer.getContext('2d');

        pointerCtx.scale(2, 2);

        const timeline = document.getElementById('timeline');
        const timelineCtx = timeline.getContext('2d');

        const canvas = {
            width: chart.width / 2,
            height: chart.height / 2,
            offsetBottom: chart.height / 10,
            offsetBottomText: 35, // отступ от текста под осью Х до оси
            offsetTextLeft: 15, // отступ слева у даты под осью Х
            axis: {
                titleColor: '#999',
                xNumbers: 6,
                offsetByLine: 9, // чтобы поднимать текст выше вертикальных линий - уменьшать значение
                offsetByDate: 15, // чтобы поднимать текст выше линии отсчета - уменьшать значение
            },
            axisStep: 50,
            lineColor: '#ddd',
            lineStep: null,
            sectorsNumber: 6,
            scaleFactor: isRetina ? 2 : 1,
            pointOrder: 10
        };

        if (isRetina) {
            chart.style.height = getSize(chart.height);
            chart.style.width = getSize(chart.width);

            pointer.style.height = getSize(pointer.height);
            pointer.style.width = getSize(pointer.width);

            timeline.style.height = getSize(timeline.height);
            timeline.style.width = getSize(timeline.width);
        }

        function getSize(size) {
            return `${parseInt(size / 2)}px`;
        }

        // pointer.addEventListener('mousemove', throttle(onPointerMouseMove, 10), false);
        pointer.addEventListener('mouseleave', onPointerMouseLeave, false);

        // pointer.addEventListener('mousemove', throttle(onPointerMouseMove, 10), false );
        pointer.addEventListener('touchstart', throttle(onPointerTouchStart, 10), false);
        pointer.addEventListener('touchend', throttle(onPointerTouchStart, 10), false);
        pointer.addEventListener('touchmove', throttle(onPointerTouchStart, 10), false); 

        function onPointerTouchStart(e) {
            const canvasOffsetLeft = chart.getBoundingClientRect().left;
            const x = e.changedTouches[0].clientX - canvasOffsetLeft;

            onPointerMouseMove(e, x);
        }

        function onPointerMouseMove(e, x = e.offsetX) {
            pointerCtx.clearRect(0, 0, canvas.width, canvas.height - canvas.offsetBottom);

            fillLine({
                x1: x,
                x2: 0,
                y1: x,
                y2: canvas.height - canvas.offsetBottom,
                ctx: pointerCtx,
                width: 2
            });
        }

        function onPointerMouseLeave(e) {
            pointerCtx.clearRect(0, 0, canvas.width, canvas.height - canvas.offsetBottom);
        }

        if (ctx) {
            fillCanvas();
        }

        function fillCanvas() {
            fillHorisontalLines();

            titleForAxisY();

            titleForAxisX();

            fillDataLines();
        }

        function fillDataLines() {
            fillGreenLine();
        }

        function fillGreenLine() {
            const exampleValues = [100, 200, 150, 30, 65, 55, 90, 200, 40].sort((a, b) => a - b);
            const pointOrder = exampleValues.length;

            fillLine({
                x1: 0,
                x2: 300,
                y1: 80,
                y2: 350,
                width: 4,
                color: 'lightgreen',
                ctx
            });

            fillLine({
                x1: 80,
                x2: 350,
                y1: 160,
                y2: 100,
                width: 4,
                color: 'lightgreen',
                ctx
            });

            fillLine({
                x1: 160,
                x2: 100,
                y1: 240,
                y2: 500,
                width: 4,
                color: 'lightgreen',
                ctx
            });

            fillLine({
                x1: 240,
                x2: 500,
                y1: 320,
                y2: 400,
                width: 4,
                color: 'lightgreen',
                ctx
            });
        }

        function fillHorisontalLines() {
            const lineStep = canvas.height / canvas.sectorsNumber;
            canvas.lineStep = lineStep;

            let counter = 0;

            while (counter < canvas.sectorsNumber) {
                const curHeight = canvas.height - counter * lineStep - 1 - canvas.offsetBottom;
                // 149, 299, 449, 599, 749 for canvas height = 350px                

                fillLine({
                    x1: 0,
                    x2: curHeight,
                    y1: canvas.width,
                    y2: curHeight,
                    ctx
                });

                counter++;
            }
        }

        function fillLine(options) {
            const { x1, x2, y1, y2, color = canvas.lineColor, width = 1, ctx } = options;

            ctx.beginPath();
            ctx.lineWidth = width;
            ctx.strokeStyle = color;
            ctx.moveTo(x1, x2);
            ctx.lineTo(y1, y2);
            ctx.stroke();
        }

        function fillText(x1, x2, text, color = canvas.axis.titleColor, font = '25px Arial') {
            ctx.font = font;
            ctx.fillStyle = color;
            ctx.fillText(text, x1, x2);
        }

        function titleForAxisY() {
            let counter = 0;

            while (counter < canvas.sectorsNumber) {
                const curHeight = canvas.height - canvas.lineStep / canvas.axis.offsetByLine - counter * canvas.lineStep - canvas.offsetBottom;
                // 140, 290, 440, 590, 740 for canvas height = 350px

                fillText(
                    0,
                    curHeight,
                    counter * canvas.axisStep
                );

                counter++;
            }
        }

        function titleForAxisX() {
            const titles = ['Jan 22', 'Jan 29', 'Feb 5', 'Feb 12', 'Feb 19', 'Feb 26'];

            canvas.axis.xStep = (canvas.width + canvas.offsetTextLeft * 2) / titles.length;

            const accuracyCoeff = 10;
            const curHeight = canvas.height - canvas.offsetBottom + canvas.offsetBottomText;

            titles.forEach((title, i) => {
                fillText(
                    canvas.offsetTextLeft + canvas.axis.xStep * i,
                    curHeight,
                    title
                );
            });
        }

        function throttle(func, ms) {
            let isThrottled = false,
                savedArgs,
                savedThis;

            function wrapper() {

                if (isThrottled) {
                    savedArgs = arguments;
                    savedThis = this;
                    return;
                }

                func.apply(this, arguments);

                isThrottled = true;

                setTimeout(function () {
                    isThrottled = false;

                    if (savedArgs) {
                        wrapper.apply(savedThis, savedArgs);
                        savedArgs = savedThis = null;
                    }
                }, ms);
            }

            return wrapper;
        }
    }

    render() {
        const content =
            <React.Fragment>
                <h3>Followers</h3>

                <div className="content">
                    <canvas id="chart" width="1000" height="750"></canvas>
                    <canvas id="pointer" width="1000" height="750"></canvas>

                    <canvas id="timeline" width="1000" height="150"></canvas>

                    <br />
                    <br />

                    <div>
                        <label>
                            <input type="radio" />Joined
                            </label>
                        <label>
                            <input type="radio" />Left
                            </label>
                    </div>
                </div>
            </React.Fragment>;

        return (
            <Document content={content} context={this.props} />
        );
    }
}

export default chart;
